.main {
	margin-top: 88px;
	@include breakpoint(sm) {
		margin-top: 49px;
	}
	@include breakpoint(lg) {
		margin-top: 139px;
	}
	@include breakpoint(xl) {
		margin-top: 188px;
	}
	@include breakpoint(xxl) {
		margin-top: 167px;
	}
}

.section {
	padding-top: 32px;
	margin-bottom: 72px;
	@include breakpoint(md) {
		padding-top: 40px;
		margin-bottom: 96px;
	}
}
.container {
	max-width: 95%;
	@include breakpoint(xxl) {
		max-width: 1300px;
	}
}
.container-fluid{
	max-width: 98%;
}

/*--------------------------------------*/
.navbar {
	background: $primary;
	display: flex;
	@include breakpoint(sm) {
		box-shadow: 0 2px 8px $gray-500;
	}
	&-brand {
		background: url("../images/logo.svg") no-repeat;
		background-size: contain;
		position: static;
		z-index: 1050;
		margin-left: 4px;
		margin-right: 12px;
		width: 56px;
		height: 72px;
		flex-shrink: 0;
		transition: all 0.2s ease;
		@include text-indent;
		@include breakpoint(sm) {
			position: absolute;
			left: 12px;
			top: 12px;
			left: 16px;
		}
		@include breakpoint(lg) {
			width: 70px;
			height: 90px;
		}
		@include breakpoint(xl) {
			width: 96px;
			height: 123px;
			left: 40px;
			top: 12px;
		}
		@include breakpoint(xxl){
			width: 104px;
			height: 134px;
		}
		&.active {
			@include breakpoint(xl) {
				width: 70px;
				height: 90px;
			}
		}
	}
	&-toggler {
		border: none;
		width: 40px;
		height: 30px;
		@include text-indent;
		background: url("../images/ic-bar.svg") no-repeat center;
		background-size: 30px;
	}
	&-collapse {
		top: 88px;
		position: fixed;
		transition: all 0.2s ease;
		box-shadow: 0 3px 5px rgba($black, 0.2);
		@include breakpoint(sm) {
			top: 49px;
		}
		@include breakpoint(lg){
			box-shadow: none;
		}
		@include breakpoint(xxl) {
			top: 57px;
		}
		&.active {
			box-shadow: 0 3px 5px rgba($black, 0.2);
			.navbar-nav {
				height: 64px;
			}
			.link-list {
				display: none;
			}
		}
	}
	&-nav {
		width: 100%;
		background: $white;
		flex-wrap: wrap;
		transition: all 0.2s ease;
		@include breakpoint(lg) {
			flex-direction: row;
			justify-content: center;
			height: 90px;
			box-shadow: none;
		}
		@include breakpoint(xl) {
			height: 110px;
		}
		&>li{
			position: relative;
			&:hover{
				@include breakpoint(lg){
					.navbar-dropdown{
					display: block !important;
				}
				}
			}
		}
		& > li:first-child {
			width: 100%;
		}
		li:last-child {
			.navbar-nav-link {
				border: none;
				&:after {
					display: none;
				}
			}
		}
		&-link {
			display: flex;
			align-items: center;
			padding: 16px;
			text-align: left;
			border-bottom: 1px solid $primary-light;
			position: relative;
			color: $primary-xdark;
			@include breakpoint(sm) {
				padding: 16px 16px 16px 92px;
			}
			@include breakpoint(lg) {
				border-bottom: none;
				width: auto;
				padding: 16px;
			}
			@include breakpoint(xl) {
				height: 100%;
			}
			@include breakpoint(xxl){
				font-size: $h5-font-size;
			}
			&:before {
				content: "";
				display: block;
				flex-shrink: 0;
				border-radius: 50%;
				width: 12px;
				height: 12px;
				background: $primary;
				margin-right: 8px;
				@include breakpoint(xl) {
					width: 15px;
					height: 15px;
				}
			}
			&:after {
				content: "";
				display: none;
				position: absolute;
				width: 1px;
				height: 18px;
				right: 0;
				top: calc(50% - 9px);
				background: $primary;
				@include breakpoint(lg) {
					display: block;
				}
			}
			&.collapsed{
				color: $black;
				img{
					transform: rotate(180deg);
				}
			}
			img{
				transition: all 0.2s ease;
			}
		}
	}
	&-dropdown{
		background: $primary-xxlight;
		width: 100%;
		padding: 0 34px;
		transition: all 0.2s ease;
		@include breakpoint(sm){
			padding: 0 34px 0 110px;
		}
		@include breakpoint(lg){
			position: absolute;
			left: calc(50% - 115px);
		top: 90%;
		width: 230px;
		border-radius: 10px !important;
		padding: 0 24px;
		}
		@include breakpoint(xxl){
			top: 80%;
		}
		li:last-child{
			.navbar-dropdown-link{
				border-bottom: none;
			}
		}
		&-link{
			display: block;
			color: $gray-700;
			padding: 12px 0;
			border-bottom: 1px solid $primary-light;
			&:hover{
				color: $primary;
			}
		}
	}
	.key {
		position: fixed;
		left: 4px;
		top: 8px;
	}
}
.nav {
	&-item {
		display: flex;
		flex-shrink: 0;
		&:before {
			content: "";
			display: block;
			width: 1px;
			height: 24px;
			background: $gray-300;
			position: absolute;
			right: 0;
			top: calc(50% - 12px);
			@include breakpoint(lg) {
				display: none;
			}
		}
		&:last-child {
			&::before {
				display: none;
			}
		}
	}
	&-link {
		display: block;
		color: $primary;
		padding: 22px 20px !important;
		position: relative;
		font-weight: $font-weight-bold;
		@include breakpoint(xl) {
			padding: 16px 10px !important;
			font-size: $h7-font-size;
		}
		@include breakpoint(lg) {
			width: 100%;
			padding: 12px 16px !important;
			text-align: center;
		}
		&:after {
			content: "";
			display: block;
			width: 0;
			height: 4px;
			background: $primary;
			position: absolute;
			left: 8px;
			bottom: 0;
			transition: all 0.2s ease;
			@include breakpoint(lg) {
				display: none;
			}
		}
		&:hover {
			color: $primary;
			&:after {
				width: calc(100% - 16px);
			}
		}
	}
}
.top {
	display: flex;
	flex-wrap: nowrap !important;
	align-items: start !important;
	padding-top: 8px;
	padding-bottom: 8px;
	@include breakpoint(sm) {
		padding-left: 80px;
		align-items: center !important;
	}
	@include breakpoint(lg) {
		padding-left: 140px;
		align-items: center !important;
	}
	@include breakpoint(xxl){
		padding-top: 12px;
	padding-bottom: 12px;
	}
	&-btn {
		display: flex;
		flex-shrink: 0;
		position: relative;
		&-item {
			display: flex;
			align-items: center;
			border-radius: 50px;
			padding: 2px 8px;
			border: 1px solid $white;
			margin-right: 8px;
			color: $white !important;
			font-size: $h7-font-size;
			@include breakpoint(lg) {
				padding: 4px 12px;
				margin-right: 12px;
			}
			@include breakpoint(xxl){
				padding: 4px 24px;
			}
			&:hover {
				color: $white;
			}
			&:last-child {
				margin-right: 0;
			}
			&[data-style="white"] {
				background: $white !important;
				color: $black !important;
				&:hover {
					border: 1px solid $primary-dark;
				}
			}
			img {
				width: 14px;
				margin-right: 4px;
				@include breakpoint(lg) {
					width: 20px;
				}
			}
		}
	}
}
.top-function {
	padding: 0 24px;
	flex-shrink: 0;
	&-list {
		display: flex;
		align-items: center;
		&-item {
			margin-right: 4px;
			&:last-child {
				margin-right: 0;
			}
			button {
				border-radius: 50%;
				border: 2px solid $white !important;
				width: 29px;
				height: 29px;
				padding: 0;
				display: flex;
				justify-content: center;
				align-items: center;
				color: $gray-800;
				background: $white;
				line-height: 0.9;
				font-size: $h7-font-size;
				@include breakpoint(xl) {
					font-size: $h6-font-size;
				}
				&:hover {
					background: $primary-dark !important;
					color: $white !important;
				}
				&.active {
					background: $primary-dark;
					color: $white;
				}
			}
		}
	}
}
/*--------------------footer------------------*/
.footer {
	position: relative;
	background: $primary-xxlight;
	padding-top: 40px;
	font-size: $h7-font-size;
	@include breakpoint(lg) {
		font-size: $h6-font-size;
	}
	&::before {
		content: "";
		display: block;
		position: absolute;
		top: -28px;
		left: 0;
		width: 100%;
		height: 28px;
		background: url("../images/footer-top.svg") repeat-x;
		background-size: 150px;
		@include breakpoint(md){
			background: url("../images/footer-top.svg") repeat-x;
		background-size: 200px;
		top: -38px;
		height: 38px;
		}
		@include breakpoint(xxxl){
			background-size: 275px;
			height: 53px;
			top: -51px;
		}
	}
	&-logo{
		display: flex;
		justify-content: center;
		img{
			width: 200px;
			margin-bottom: 24px;
			@include breakpoint(sm){
				margin-bottom: 0;
			}
			@include breakpoint(lg){
				width: 260px;
			}
		}
	}
	.key {
		position: absolute;
		left: 16px;
		top: 16px;
	}
	&-btn{
		display: flex;
		align-items: center;
		color: $white !important;
		border-radius: 10px;
		font-size: $h7-font-size;
		@include breakpoint(lg) {
			font-size: $h6-font-size;
		}
	}
	&-toggler {
		display: flex;
		align-items: center;
		border-radius: 10px 10px 0 0;
		color: $white !important;
		padding: 2px 12px;
		font-size: $h7-font-size;
		@include breakpoint(lg){
			font-size: $h6-font-size;
			padding: 6px 12px;
		}
		&-icon {
			@include text-indent;
			width: 20px;
			height: 20px;
			background: url("../images/ic-minus.svg");
			background-size: 20px;
		}
		&::after {
			content: "";
			display: inline-block;
		}
		&.collapsed {
			.footer-toggler-icon {
				background: url("../images/ic-more.svg");
				background-size: 20px;
			}
			&::after {
				content: "";
				display: inline-block;
			}
		}
	}
	&-list {
		&-item{
			&:first-child{
				.footer-list-link{
					display: flex;
					align-items: center;
					padding: 8px 0;
					color: $black;
					&:before {
						content: "";
						display: block;
						flex-shrink: 0;
						border-radius: 50%;
						width: 12px;
						height: 12px;
						background: $primary;
						margin-right: 8px;
						flex-shrink: 0;
						@include breakpoint(xl) {
							width: 15px;
							height: 15px;
						}
					}
			}
			}
		}
		&-link {
			color: $gray-800;
			display: block;
			padding: 2px 0 2px 19px;
			@include breakpoint(lg){
				padding: 4px 0 4px 20px;
			}
			@include breakpoint(xl){
				padding-left: 22px;
			}
			@include breakpoint(xxxl){
				padding-left: 24px;
			}
			&:hover {
				color: $primary;
			}
		}
	}
	&-qrcode {
		background: $white;
		border-radius: 4px;
		padding: 2px;
		flex-shrink: 0;
		width: 80px;
		@include breakpoint(xl) {
			border-radius: 10px;
			width: 96px;
		}
		@include breakpoint(xxl) {
			width: 120px;
		}
	}
	&-function {
		background: $secondary;
		position: fixed;
		right: 0;
		width: 52px;
		bottom: 72px;
		border-radius: 12px 0 0 12px;
		border: 1px solid $white;
		z-index: 100;
		box-shadow: 0 3px 6px $gray-500;
		padding: 0 12px;
		@include breakpoint(sm) {
			padding: 0 16px;
			width: 72px;
			border-radius: 16px 0 0 16px;
		}
		@include breakpoint(xxl) {
			width: 96px;
			border-radius: 20px 0 0 20px;
		}
		&-link {
			display: flex;
			justify-content: center;
			font-size: $h7-font-size;
			font-weight: $font-weight-semibold;
			color: $white;
			border-bottom: 1px solid $white;
			width: 100%;
			padding: 12px 0;
			@include breakpoint(md) {
				font-size: 1.3rem;
				padding: 16px 0;
			}
			&:last-child {
				border-bottom: none;
			}
			&[data-con="ai"] {
				background: url("../images/ic-ai.svg") center no-repeat;
				background-size: 26px;
				@include text-indent;
				height: 48px;
				@include breakpoint(sm) {
					height: 56px;
				}
				@include breakpoint(md) {
					background: url("../images/ic-ai.svg") center no-repeat;
					background-size: 32px;
					height: 64px;
				}
			}
			&:hover {
				opacity: 0.8;
			}
		}
	}
}

