/*--------------------------------------*/
.backdrop {
	width: 100vw;
	height: 100vh;
	position: absolute;
	left: 0;
	top: 0;
	display: none;
	z-index: 100;
	&.show {
		display: block;
	}
}
.offcanvas-backdrop.show {
	opacity: 0.7;
}
.offcanvas-header {
	padding-left: 20px;
	padding-right: 32px;
	.btn {
		&:active,
		&:focus {
			border: 0;
		}
	}
}
.offcanvas-bottom {
	border-radius: 24px 24px 0 0;
	height: 350px !important;
	padding: 24px 0;
	.offcanvas-header {
		position: relative;
		&-close {
			position: absolute;
			background: $gray-100 !important;
			border-radius: 24px 24px 0 0;
			width: 106px;
			height: 40px;
			top: -39px;
			left: calc(50% - 53px);
			display: flex;
			justify-content: center;
			align-items: center;
			border: none;
			&::before,
			&::after {
				content: "";
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: url("../images/fillet-gray.svg");
			}
			&::before {
				bottom: -1px;
				left: -23px;
			}
			&::after {
				bottom: -1px;
				right: -23px;
				transform: rotate(90deg);
			}
			&:focus {
				background: $gray-100;
			}
		}
	}
	.offcanvas-body {
		padding: 0 66px;
		overflow-y: initial;
	}
}
.offcanvas-end {
	width: 350px !important;
	border-radius: 24px 0 0 24px;
	.offcanvas-header {
		border-radius: 24px 0 0 0;
	}
}
.offcanvas-body {
	padding: 0 32px 24px 32px;
}

.select-list-wrapper {
	overflow-y: auto;
	padding: 0 32px 24px 32px;
	flex: 1 1;
	font-size: $h6-font-size;
	.form-check-input {
		width: 16px;
		height: 16px;
		margin-top: 0;
		border: 2px solid $gray-700 !important;
		&[type="radio"] {
			border-radius: 50%;
		}
		&:checked[type="radio"] {
			border-radius: 50%;
			background: url(../images/radio-circle.svg) no-repeat center $primary;
			background-size: 6px;
			border: 2px solid $primary !important;
		}
		&[type="checkbox"] {
			border-radius: 4;
		}
		&:checked[type="checkbox"] {
			background: url(../images/ic-check.svg) no-repeat center $primary;
			background-size: 9px;
			border: 2px solid $primary !important;
		}
	}
	input[disabled] {
		border: 2px solid $gray-600;
	}
	input[disabled] ~ label {
		opacity: 1 !important;
		color: $gray-700;
	}
	input[disabled] ~ label span {
		color: $danger !important;
	}
	label {
		display: flex;
		justify-content: space-between;
		padding-left: 4;
		p {
			display: block;
			width: calc(100% - 70px);
			flex: 1 1;
			line-height: 1.3;
		}
		span {
			padding-left: 8px;
		}
	}
}
