/*--------------------------------------*/
.main {
    &-banner {
        width: 100%;
        max-height: 590px;
        overflow: hidden;
        @include breakpoint(sm) {
            display: none;
        }
    }
}
.keywords {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    @include breakpoint(md) {
        flex-wrap: wrap;
    }
    &-title {
        @include breakpoint(md) {
            width: 100%;
        }
    }
    &-list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        @include breakpoint(md) {
            width: 100%;
        }
        &:before {
            content: "";
            display: block;
            width: 14px;
            height: 12px;
            background: url("../images/ic-arrow-right.svg") no-repeat;
            background-size: contain;
            margin-right: 16px;
            @include breakpoint(lg) {
                margin-right: 0;
            }
        }
    }
    &-item {
        border-right: 1px solid $primary-light;
        &:last-child {
            border: none;
        }
    }
    &-link {
        color: $primary;
        padding: 0 24px;
        @include breakpoint(lg) {
            padding: 0 16px;
            font-size: $h7-font-size;
        }
        @include breakpoint(sm){
            padding: 0 12px;
            font-size: $h8-font-size;
        }
        @include breakpoint(sm){
            padding: 0 10px;
        }
        &:hover {
            color: $primary-dark;
        }
    }
    &-btn {
        @include breakpoint(md) {
            position: absolute;
            right: 12px;
            top: 16px;
            font-size: $h7-font-size;
            img{
                width: 20px;
            }
        }
    }
}
.main-list{
    &[data-size="lg"]{
        .main-list-btn{
            img{
                @include breakpoint(md){
                    width: 48px;
                }
            }
        } 
    }
    &[data-con="multi-btn"]{
        .main-list-btn{
            flex-direction: row;
            align-items: center;
            padding: 32px;
            text-align: left;
            height: 145px;
            justify-content: flex-start;
            @include breakpoint(lg){
                height: 90px !important;
            }
            img{
                width: 50px;
                @include breakpoint(lg){
                    height: 30px;
                }
            }
            p{
                color: $black;
                font-size: $h6-font-size;
                font-weight: $font-weight-medium;
                @include over_text(1);
                @include breakpoint(lg){
                    font-size: $h7-font-size;
                }
            }
        }
    }
    &-btn{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: $white !important;
        border-radius: 16px;
        border: 1px solid $white;
        box-shadow: 0 5px 7px $gray-300;
        width: 100%;
        height: 220px;
        color: $primary-dark;
        @include breakpoint(lg){
            flex-direction: row;
            height: 100px !important;
        }
        @include breakpoint(md){
            font-size: $h6-font-size;
        }
        &:hover{
            border: 1px solid $primary;
            box-shadow: none;
        }
        &-title{
            font-size: $h4-font-size;
        font-weight: $font-weight-semibold;
        @include breakpoint(lg){
            font-size: $h5-font-size;
            margin-bottom: 0;
            width: 195px;
        }
        @include breakpoint(md){
            font-size: $h6-font-size;
            width: 165px;
        }
        }
        img{
            max-width: 45%;
            @include breakpoint(lg){
                width: 52px;
            }
            @include breakpoint(md){
                max-width: 25%;
            }
            @include breakpoint(sm){
                max-width: 40%;
            }
        }
    }
}
.btn-list{
    &-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        background: $white;
        border-radius: 16px;
        border: 1px solid $white !important;
        box-shadow: 0 5px 7px $gray-300;
        width: 100%;
        height: 150px;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include breakpoint(lg){
            height: 100px !important;
        }
        @include breakpoint(md){
            font-size: $h6-font-size;
        }
        &:hover{
            border: 1px solid $primary !important;
            box-shadow: none;
        }
        img{
            width: 56px;
            @include breakpoint(lg){
                width: 52px;
            }
            @include breakpoint(md){
                width: 48px;
            }
        }
    }
    p{
        padding: 0 16px;
        text-align: center;
        @include breakpoint(lg){
            width: 195px;
        }
        @include breakpoint(md){
            width: 165px;
            padding: 0 8px;
        }
    }
}
.theme-card{
    width: 90%;
    max-width: 1286px;
    margin: 0 auto;
    padding: 0 80px;
    @include breakpoint(lg){
        width: 100%;
        padding: 0 72px;
    }
    @include breakpoint(xl){
        padding: 0 68px;
    }
    @include breakpoint(lg){
        padding: 0 calc(5% - 12px);
    }
    @include breakpoint(md){
        width: 100%;
        max-width: 95%;
        padding: 0 10px;
    }
    @include breakpoint(sm){
        padding: 0 2%;
    }
    @include breakpoint(sm){
        max-width: 98%;
        padding: 0 12px;
    }
    .slick-slide:nth-child(3n){
        .theme-card-item{
            margin-right: 3px;
            @include breakpoint(xl){
                margin-right: 24px;
            }
        }
    }
    .slick-slide:nth-child(2n){
        .theme-card-item{
            @include breakpoint(xl){
                margin-right: 3px;
            }
        }
    }
    &-item{
        display: block;
        background: $white;
        border-radius: 16px;
        overflow: hidden;
        border: 3px solid $gray-300;
        position: relative;
        margin: 0 24px 12px 0;
        @include breakpoint(sm){
            margin: 0 0 12px 0 !important;
        }
        p{
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            height: 135px;
            font-size: $h5-font-size;
            font-weight: $font-weight-medium;
            color: $black;
            padding: 0 16px;
            @include breakpoint(md){
                height: 100px;
                font-size: $h6-font-size;
            }
            @include breakpoint(sm){
                padding: 0 40px;
            }
        }
        span{
            display: block;
            background: $primary-light;
            color: $primary-dark;
            text-align: center;
            font-size: $h7-font-size;
            font-weight: $font-weight-medium;
            padding: 8px 8px;
            @include breakpoint(md){
                font-size: $h8-font-size;
            }
        }
        &:hover{
            border: 3px solid $primary-light;
            .theme-card-tag{
                &::before{
                    width: 100%;
                }
            }
        }
    }
    &-tag{
        border-radius: 0 8px 8px 0;
        background: $primary-light;
        position: absolute;
        width: 50px;
        height: 35px;
        left: 0;
        top: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        overflow: hidden;
        @include breakpoint(lg){
            width: 40px;
            height: 30px;
        }
        @include breakpoint(md){
            top: 35px;
        }
        &:before{
            content: "";
            display: block;
            position: absolute;
            left: 0;
            width: 0;
            height: 100%;
            background: $tertiary;
            transition: all 0.2s ease;
        }
        img{
            width: 25px;
            position: relative;
            z-index: 10;
        }
    }
}