@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+TC:wght@200..900&display=swap');

/* ----------------------------- */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	width: 100%;
	-webkit-text-size-adjust: none;
}
html{
	&.sm {
		font-size: 0.9rem;
	}
	&.lg {
		font-size: 1.1rem;
	}
}
body {
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	font-family: "Arial", "Noto Sans TC", "Microsoft JhengHei", "sans-serif";
	line-height: 1.5;
	letter-spacing: 0.5px;
	color: $gray-900;
	font-weight: $font-weight-medium;
	/**/
	-webkit-user-select:none;
	-webkit-touch-callout:none;
	-moz-user-select: none;
}
img {
	display: block;
	max-width: 100%;
	height: auto;
}
a,
a:hover,
button,
button:hover {
	transition: all 0.2s ease;
	text-decoration: none;
}
a:focus,button:focus{
	outline: 2px dashed $primary !important;
	box-shadow: none !important;
}
p {
	margin-bottom: 0;
}
ul {
	padding-left: 0;
	list-style: none;
	margin-bottom: 0;
}
ol {
	margin-bottom: 0;
}
input,textarea{
	border: 1px solid $primary !important;
	@include breakpoint(sm){
		font-size: $h7-font-size !important;
	}
}
select{
	border: 1px solid $primary !important;
	background: url("../images/ic-select-down.svg") no-repeat right center $white !important;
	padding-right: 24px !important;
	@include breakpoint(sm){
		font-size: $h7-font-size !important;
	}
}
.select2-dropdown{
	border: none;
	box-shadow:  0 5px 5px $gray-600;
}
.select2-selection__arrow{
	background: url("../images/ic-select-down.svg") no-repeat center $white;
	height: calc(100% - 2px) !important;
	b{
		display: none;
	}
}

.select2-results__message{
	font-size: $h7-font-size;
	color: $primary;
	padding-left: 12px;
}
input::placeholder,textarea::placeholder {
	color: $gray-500!important;
	@include breakpoint(sm){
		font-size: $h7-font-size !important;
	}
}
.form-check-input:checked[type="radio"]{
	background: url("../images/radio-check.svg") no-repeat center;
	background-size: 8px;
}
input[readonly]{
	border: 1px solid $primary-light !important;
}
.form-check,select~label{
	@include breakpoint(sm){
		font-size: $h7-font-size !important;
	}
}
