/*--------------------------------------*/
.main-title {
    color: $black;
    font-size: $h3-font-size;
    border-bottom: 2px solid $primary;
    font-weight: $font-weight-bold;
    text-align: center;
    padding-bottom: 16px;
    @include breakpoint(lg) {
        font-size: $h4-font-size;
    }
    @include breakpoint(md) {
        font-size: $h5-font-size;
    }
    &[data-style="noline"] {
        border-bottom: 0;
        padding-bottom: 0;
    }
}
.sub-title {
    font-size: $h5-font-size;
    border-bottom: 1px solid $primary;
    font-weight: $font-weight-bold;
    padding-bottom: 8px;
    display: flex;
    align-items: center;
    @include breakpoint(md) {
        font-size: $h6-font-size;
        line-height: 1.5;
    }
    img {
        width: 32px;
        margin-right: 8px;
    }
}
/*--------------------------------------*/
.input-group {
    width: 100%;
    max-width: 500px;
    @include breakpoint(sm) {
        max-width: 300px;
    }
    @include breakpoint(md) {
        max-width: 500px !important;
    }
    @include breakpoint(xxl){
        max-width: 612px !important;
    }
    .form-control {
        border-radius: 50px 0 0 50px;
        border: none !important;
        font-size: $h7-font-size;
        @include breakpoint(lg) {
            font-size: $h6-font-size;
        }
    }
    button {
        border-radius: 0 50px 50px 0;
        background: $white !important;
        border: none !important;
        height: 100%;
        padding: 0 8px;
        @include breakpoint(lg) {
            padding: 0 12px;
        }
    }
}
/*--------------------------------------*/
.link-img {
    display: inline-block;
    &:hover {
        opacity: 0.8;
    }
}
.link-list {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    font-size: $h7-font-size;
    padding: 24px 0 8px 16px;
    transition: all 0.2s ease;
    @include breakpoint(sm) {
        padding: 24px 0 8px 92px;
    }
    @include breakpoint(lg) {
        justify-content: center;
        padding: 16px 0 0 0;
    }
    @include breakpoint(xxl) {
        font-size: $h6-font-size;
        padding: 0;
    }
    &-link {
        color: $primary-dark;
        padding-right: 12px;
        margin-right: 12px;
        border-right: 1px solid $gray-400;
        line-height: 1;
        @include breakpoint(xxl) {
            color: $white;
            border-right: 1px solid $white;
        }
        &:hover {
            opacity: 0.8;
        }
    }
    li:last-child {
        .link-list-link,span {
            border: none;
            margin-right: none;
            padding-right: 0;
            margin-right: 0;
        }
    }
    &[data-con="footer"]{
        .link-list-link{
            color: $primary-xdark;
        padding-right: 12px;
        margin-right: 12px;
        border-right: 1px solid $primary-xdark;
        }
    }
}
/*--------------------------------------*/
.tab {
    &-menu {
        display: flex;
        width: 165px;
        @include breakpoint(lg) {
            width: 100%;
        }
    }
    &-menu-btn {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        background: $gray-100;
        border: 1px solid $primary-light;
        color: $gray-700;
        border-radius: 0 !important;
        padding: 9px 12px;
        font-weight: $font-weight-semibold;
        font-size: $h5-font-size;
        @include breakpoint(md) {
            flex-direction: column;
            font-size: $h6-font-size;
            padding-bottom: 15px;
        }
        &:hover {
            border: 1px solid $primary;
        }
        &.active {
            border: 1px solid $primary;
            border-left: 5px solid $primary;
            color: $primary;
            @include breakpoint(md) {
                border: 1px solid $primary;
                padding-bottom: 15px;
                position: relative;
                &:after {
                    content: "";
                    display: block;
                    position: absolute;
                    left: 0;
                    bottom: 0;
                    width: 100%;
                    height: 100%;
                    border: 2px solid $primary;
                    border-bottom-width: 5px;
                }
            }
            img {
                filter: invert(33%) sepia(95%) saturate(640%) hue-rotate(134deg) brightness(60%) contrast(102%);
            }
        }
        img {
            width: 24px;
        }
    }
    &-action {
        display: flex;
        justify-content: center;
        @include breakpoint(lg) {
            width: 100%;
        }
    }
}
@each $color, $value in $theme-colors {
    .tag-#{$color} {
        width: 50px;
        display: flex;
        justify-content: center;
        border: 1px solid $value;
        border-radius: 5px;
        color: $value;
        font-size: $h7-font-size;
        padding: 0;
        @include breakpoint(md) {
            font-size: $h8-font-size;
        }
    }
}
/*--------------------------------------*/
.tag-list {
    background: $white;
    &-item {
        border-bottom: 1px solid $primary-light;
        &:last-child {
            border-bottom: none;
        }
    }
    &-link {
        display: flex !important;
        align-items: start;
        color: $gray-800;
        padding: 16px 0;
        font-size: $h6-font-size;
        @include breakpoint(lg) {
            padding: 12px 0;
        }
        @include breakpoint(sm) {
            font-size: $h7-font-size;
        }
        &:before {
            content: "";
            display: block;
            width: 7px;
            height: 17px;
            margin-right: 16px;
            margin-top: 2px;
            background: $tertiary;
            flex-shrink: 0;
            @include breakpoint(sm) {
                margin-right: 12px;
            }
        }
        &:hover {
            text-decoration: underline !important;
        }
        p {
            @include over_text(2);
        }
    }
}
.icon-list {
    display: flex;
    flex-direction: row;
    font-size: $h7-font-size;
    flex-wrap: wrap;
    @include breakpoint(md) {
        flex-direction: column;
        font-size: $h6-font-size;
    }
    &-item {
        display: flex;
        margin-right: 16px;
        align-items: start;
        &:last-child {
            margin-right: 0;
            margin-bottom: 0;
        }
        img {
            width: 20px;
            margin-right: 4px;
            margin-top: 2px;
            @include breakpoint(md) {
                margin-right: 8px;
            }
        }
    }
}
/*--------------------------------------*/
.table-rwd {
    min-width: 100%;
    @include breakpoint(lg) {
        min-width: 90%;
        margin: 0 auto;
    }
    tr {
        border-bottom: 1px solid $primary-light;
        @include breakpoint(lg) {
            border: 1px solid $primary;
            border-top: 8px solid $primary;
        }
    }
    th {
        display: table-cell;
        background: $primary;
        color: $white;
        font-weight: normal;
        vertical-align: middle;
        font-size: $h6-font-size;
        padding: 8px;
        @include breakpoint(lg) {
            display: none;
        }
    }
    td {
        display: table-cell;
        vertical-align: middle;
        padding: 16px 8px;
        @include breakpoint(lg) {
            display: flex;
            text-align: left !important;
            align-items: start;
            padding: 4px 20px !important;
            position: relative;
            font-size: $h7-font-size;
            &[data-th=""] {
                &::before {
                    content: "";
                    display: none;
                }
            }
        }
        &:first-child {
            @include breakpoint(lg) {
                padding-top: 16px !important;
            }
        }
        &:last-child {
            @include breakpoint(lg) {
                padding-bottom: 16px !important;
            }
        }
        &:before {
            display: none;
            @include breakpoint(lg) {
                flex-shrink: 0;
                content: attr(data-th) "";
                display: inline-block;
                color: $primary;
                font-weight: $font-weight-medium;
            }
        }
    }
    a {
        color: $bright;
        &:hover {
            opacity: 0.8;
        }
    }
}
.table-group {
    display: flex;
    align-items: center;
    width: 100%;
    @include breakpoint(sm) {
        flex-direction: column;
        align-items: start;
    }
}
.table-sign {
    margin: 0 auto;
    @include breakpoint(lg) {
        margin: 0;
        display: inline-block;
    }
}

/*--------------------------------------*/
.scrollbar {
    width: 100%;
    overflow: auto;
    position: relative;
    -webkit-overflow-scrolling: touch;
    &::-webkit-scrollbar-track {
        background-color: $gray-300;
    }
    &::-webkit-scrollbar {
        width: 4px;
        height: 4px;
        background-color: $gray-300;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        background-color: $gray-500;
    }
}
/*--------------------------------------*/
.modal {
    &-header {
        align-items: center;
        border-bottom: none;
        position: relative;
        font-size: $h5-font-size;
        font-weight: $font-weight-semibold;
        padding-left: 24px;
        padding-right: 24px;
        .btn-close {
            background: url("../images/ic-close.svg") no-repeat center;
            background-size: 80%;
            opacity: 1;
            @include text-indent;
            &:hover {
                opacity: 0.8;
            }
        }
    }
    &-icon{
        width: 40px;
    }
    &-content {
        border-radius: 16px !important;
    }
    &-body {
        font-size: $h6-font-size;
        @include breakpoint(sm) {
            font-size: $h7-font-size;
        }
    }
}
/*--------------------------------------*/
.accordion {
    &-body {
        position: relative;
        @include breakpoint(md) {
            font-size: $h6-font-size;
        }
        &::before {
            content: "";
            display: block;
            position: absolute;
            width: calc(100% - 32px);
            height: 1px;
            left: 16px;
            top: 0;
            background: $primary-light;
        }
    }
    &-item {
        border: none;
    }
    &-button {
        background: $white;
        color: $primary;
        border-radius: 8px !important;
        padding: 12px 48px 12px 32px;
        position: relative;
        line-height: 1.3;
        box-shadow: none !important;
        font-size: $h5-font-size;
        font-weight: $font-weight-medium;
        @include breakpoint(md) {
            padding: 12px 32px 12px 18px;
        }
        &:after {
            background: url(../images/ic-add-active.svg);
            background-size: 100%;
            position: absolute;
            right: 32px;
            top: calc(50% - 10px);
            width: 24px;
            height: 24px;
            @include breakpoint(md) {
                right: 16px;
                width: 18px;
                height: 18px;
                top: calc(50% - 9px);
            }
        }
        &:not(.collapsed) {
            background: $white;
            color: $secondary;
        }
        &:not(.collapsed)::after {
            background: url(../images/ic-minus-default.svg);
            background-size: 100%;
            transform: rotate(0deg);
        }
    }
}
/*--------------------------------------*/

.page {
    display: flex;
    justify-content: center;
    align-items: center;
    @include breakpoint(sm) {
        flex-direction: column;
        align-items: center;
    }
    &-list {
        display: flex;
        justify-content: flex-end;
        border-right: 1px solid $gray-300;
        padding-right: 16px;
        margin-right: 16px;
        @include breakpoint(sm) {
            margin-bottom: 12px;
            border-right: none;
            padding-right: 0;
            margin-right: 0;
        }
    }
    &-item {
        padding: 0 4px;
        display: flex;
        align-items: center;
        &:nth-child(1),
        &:nth-child(2),
        &:nth-last-child(1),
        &:nth-last-child(2) {
            .page-link {
                border: 1px solid $gray-200;
                background: $gray-100;
            }
        }
        .form-control {
            width: 40px;
            height: 28px;
            color: $primary;
            border: 1px solid $primary;
            border-radius: 5px;
            margin: 0 5px;
            padding: 0 8px;
            text-align: center;
        }
    }
    &-link {
        border-radius: 4px;
        background: $white;
        border: 1px solid $primary;
        color: $primary;
        padding: 0;
        width: 28px;
        height: 28px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 0.2s ease;
        &:hover {
            background: $gray-100;
        }
        &.active {
            border: 2px solid $primary;
            font-weight: $font-weight-medium;
        }
        img {
            width: 25px;
            margin-right: 0;
        }
        &-function {
            border-radius: 4px;
        }
    }
    &-form {
        display: flex;
        align-items: center;
        flex-shrink: 0;
        color: $gray-600;
        span {
            flex-shrink: 0;
        }
        select {
            width: 64px;
            height: 28px;
            flex-shrink: 0;
            padding-top: 0;
            padding-bottom: 0;
        }
    }
}
