/*----*/
@mixin over_text($line) {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
}

/*----*/
@mixin text-indent {
    text-indent: -9999px;
    overflow: hidden;
    white-space: nowrap;
}

/* --RWD-- */
@mixin breakpoint($breakpoint) {
  @if map-has-key($grid-breakpoints, $breakpoint) {
    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {
      @content;
    }
  } @else {
    @warn ": #{$breakpoint}: #{map-keys($grid-breakpoints)}";
  }
}
/* ---- */
@mixin gradient-horizon($colorstart, $colorend) {
    background: $colorstart;
    /* Old browsers */
    background: -moz-linear-gradient(left, $colorstart 0%, $colorend 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(left, $colorstart 0%, $colorend 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to right, $colorstart 0%, $colorend 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}