.main {
    min-height: 603px;
    @include breakpoint(md) {
        min-height: auto;
    }
    &-header-wrapper {
        background: url("../images/header-bg.jpg") no-repeat;
        background-size: cover;
    }
    &-header {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        @include breakpoint(md) {
            align-items: center;
            flex-direction: column;
        }
        &[data-con="apply"] {
            img {
                filter: invert(33%) sepia(95%) saturate(640%) hue-rotate(134deg) brightness(60%) contrast(102%);
            }
        }
    }
    &-top {
        display: flex;
        align-items: center;
        font-weight: $font-weight-semibold;
        @include breakpoint(md) {
            font-size: $h4-font-size;
        }
        img {
            width: 50px;
            @include breakpoint(md) {
                width: 32px;
            }
        }
    }
}
.breadcrumb {
    align-items: flex-start;
    justify-content: flex-end;
    padding: 8px 0;
    font-size: $h7-font-size;
    &::before {
        content: "";
        display: block;
        margin-top: 4px;
        margin-right: 8px;
        width: 14px;
        height: 14px;
        background: url("../images/ic-home.svg") no-repeat center;
    }
    &-link {
        color: $gray-800;
        &:hover {
            color: $primary;
        }
    }
    &-item {
        color: $gray-800;
        display: block;
    }
    &-bg {
        background: url("../images/breadcrumb-bg.jpg") no-repeat;
        background-size: cover;
    }
}
.breadcrumb-item + .breadcrumb-item {
    padding-left: 4px;
}
.breadcrumb-item + .breadcrumb-item::before {
    content: "";
    display: inline-block;
    width: 16px;
    height: 24px;
    background: url("../images/ic-route.svg") no-repeat center;
    margin-right: 4px;
}
/*--------------------------------------*/
.side-menu {
    position: fixed;
    width: 250px;
    height: calc(100vh - 88px);
    flex-shrink: 0;
    transition: all 0.2s ease;
    left: 0;
    top: 88px;
    padding: 24px;
    z-index: 150;
    background: $secondary-light;
    @include breakpoint(sm) {
        top: 50px;
        height: calc(100vh - 50px);
        padding: 72px 24px 32px 24px;
    }
    @include breakpoint(lg) {
        position: relative;
        width: 280px;
        top: auto !important;
        height: 100% !important;
        margin-top: 40px !important;
        border-radius: 0 24px 24px 0;
        padding-top: 32px;
    }
    @include breakpoint(xxl) {
        width: 300px;
        padding: 40px 32px 40px 80px;
    }
    &-body {
        border-radius: 20px;
        position: relative;
        &:after {
            content: "";
            display: block;
            position: absolute;
            width: 225px;
            height: 138px;
            background: url("../images/side-menu-p1.svg");
            left: -16px;
            bottom: -32px;
        }
    }
    &-item {
        background: none;
        border: none;
        border-bottom: 1px solid $primary-light;
        border-radius: 0 !important;
        margin-bottom: 16px;
        @include breakpoint(md) {
            margin-bottom: 16px;
        }
    }
    &-btn {
        background: none !important;
        border: none !important;
        box-shadow: none !important;
        border-radius: 0 !important;
        color: $tertiary !important;
        justify-content: center;
        position: relative;
        padding-bottom: 8px;
        font-weight: $font-weight-bold;
        padding: 12px 0;
        @include breakpoint(xl) {
            font-size: $h6-font-size;
        }
        @include breakpoint(lg) {
            font-size: $h4-font-size;
        }
        &:before {
            content: " ";
            display: block;
            position: absolute;
            left: 0;
            bottom: -8px;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background: $primary-light;
            border: 4px solid $secondary-light;
        }
    }
    &-btn:not(.collapsed) {
        &:after {
            transform: rotate(0deg);
        }
    }
    &-toggler {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        color: $white !important;
        width: 32px;
        height: 40px;
        border-radius: 0 8px 8px 0 !important;
        position: absolute;
        right: -32px;
        top: 56px;
        line-height: 1.2;
        z-index: 10;
        transition: all 0.2s ease;
        border: none;
        &:hover,
        &:active,
        &:focus {
            outline: none !important;
        }
        img {
            width: 12px;
            margin-right: 0;
            margin-bottom: 3px;
            transform: rotate(180deg);
        }
    }
    &.close {
        left: -250px;
        @include breakpoint(lg) {
            left: auto;
        }
        .side-menu-toggler {
            img {
                transform: rotate(0deg);
            }
        }
    }
    &.fixed {
        position: fixed;
        top: 71px;
    }
}
.side-content {
    padding-left: 3%;
    padding-right: 3%;
    margin-left: 0;
    width: 100%;
    @include breakpoint(xl) {
        padding-left: 4%;
        padding-right: 4%;
    }
    @include breakpoint(xxl) {
        padding-left: 5%;
        padding-right: 8%;
    }
}
.market-card {
    display: block;
    border-radius: 24px;
    background: $white;
    padding: 16px;
    position: relative;
    text-align: center;
    border: 1px solid $white;
    &:hover {
        border: 1px solid $primary-light;
    }
    &-tag {
        width: 48px;
        height: 48px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        background: $white;
        border: 1px solid $quaternary;
        color: $quaternary;
        position: absolute;
        left: 8px;
        top: 8px;
    }
    &-pic {
        border-radius: 16px;
        object-fit: cover;
        width: 100%;
        height: 188px;
        margin-bottom: 12px;
    }
    &-title {
        color: $black;
        font-size: $h4-font-size;
        margin-bottom: 4px;
    }
    &-info {
        font-size: $h6-font-size;
        color: $gray-700;
    }
    &-more {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        color: $primary-dark;
        font-size: $h7-font-size;
        img {
            margin-top: 2px;
            margin-left: 2px;
        }
    }
}
.section{
    &-title{
        font-weight: $font-weight-semibold;
        font-size: $h3-font-size;
        @include breakpoint(xxl){
            font-size: $h2-font-size;
        }
    }
    &-subtitle{
        display: flex;
        align-items: center;
        color: $tertiary;
        font-size: $h5-font-size;
        &:after{
            content: "";
            width: 100%;
            height: 1px;
            background: $tertiary;
            margin-left: 8px;
        }
        span{
            flex-shrink: 0;
        }
    }
    &-btn{
        display: flex;
        align-items: center;
        border-radius: 50px;
        line-height: 1;
        background: $white !important;
        color: $gray-600;
        padding: 2px 12px;
        font-weight: $font-weight-medium;
        border: 1px solid $gray-500;
        font-size: $h7-font-size;
        @include breakpoint(lg){
            font-size: $h6-font-size;
            padding: 4px 12px;
        }
        &:hover{
            border: 1px solid $primary-light;
            color: $primary
        }
    }
    &-pic{
        width: 100%;
        height: 250px;
        object-fit: cover;
        border-radius: 20px;
        overflow: hidden;
        border: 3px solid $white;
        @include breakpoint(md){
            height: 300px;
        }
        @include breakpoint(lg){
            height: 350px;
        }
        &[data-style="long"]{
            @include breakpoint(lg){
                height: 450px;
            }
        }
        img{
            object-fit: cover;
            width: 100%;
        height: 350px;
        }
    }
    &-thumb {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        border-radius: 20px;
        overflow: hidden;
        height: 110px;
        border: 3px solid $white;
        box-shadow: 0 3px 8px $gray-500;
        @include breakpoint(lg) {
            height: 140px;
        }
        &:hover {
            img {
                transform: scale(1.1);
            }
            .section-thumb-icon {
                width: 34px;
                height: 34px;
                background: $primary-dark;
            }
        }
        &-icon {
            position: absolute;
            width: 30px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            bottom: 12px;
            right: 12px;
            z-index: 10;
            transition: all 0.2s ease;
            background: $primary;
            border: 2px solid $white;
        }
        & > img {
            max-width: initial;
            transition: all 0.2s ease;
            object-fit: cover;
            object-position: center;
            width: 100%;
            height: 100%;
        }
    }
}
.section-video {
    display: block;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    border: 3px solid $white;
    @include breakpoint(md){
        height: 100%;
    }
    &:hover {
        .section-video-play {
            background: $white;
            transition: all 0.2s ease;
        }
    }
    &-action {
        display: flex;
        align-items: center;
        position: absolute;
        left: 24px;
        bottom: 24px;
        font-weight: $font-weight-bold;
        color: $primary-xdark;
        font-size: $h7-font-size;
        line-height: 1;
        @include breakpoint(lg){
            font-size: $h6-font-size;
            line-height: 1.4;
        }
        &~img{
            @include breakpoint(md){
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            @include breakpoint(lg){
                height: auto;
            }
        }
    }
    &-play {
        display: flex;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        justify-content: center;
        align-items: center;
        background: $primary-xdark;
        border: 3px solid $white;
        margin-right: -40px;
        position: relative;
        z-index: 10;
        color: $primary-dark;
        background: rgba(255, 255, 255, 0.3); /*  */
        backdrop-filter: blur(10px); /*  */
        -webkit-backdrop-filter: blur(10px); /* Safari  */
        box-shadow: inset 0 0 3px $gray-800;
        @include breakpoint(lg){
            width: 64px;
        height: 64px;
        border: 5px solid $white;
        }
        img {
            width: 16px;
            @include breakpoint(lg){
                width: 24px;
            }
        }
    }
}
.slider-container {
    .main-slider {
        height: 350px;
        @include breakpoint(lg){
            height: 500px;
        }
        img {
            width: 100%;
            height: 350px;
            border-radius: 20px 20px 0 0;
            object-fit: cover;
            @include breakpoint(lg){
                height: 500px;
            }
        }
    }
    .thumbnail-slider {
        background: $white;
        border-radius: 0 0 20px 20px;
        padding: 12px;
        .slick-slide {
            width: 68px !important;
            height: 68px;
            overflow: hidden;
            margin: 0 6px;
            border-radius: 20px;
            border: 3px solid $gray-200;
            cursor: pointer;
            &.slick-current {
                border: 3px solid $primary-light;
            }
            img {
                width: 68px;
                height: 68px;
                object-fit: cover;
            }
        }
        .slick-track{
            display: flex;
            justify-content: center;
        }
    }
}

.link-group {
    border: 1px solid $primary-light;
    align-items: center;
    min-height: 94px;
    &-text {
        @include over_text(2);
        font-weight: $font-weight-medium;
        font-size: $h5-font-size;
        padding-right: 16px;
        width: 100%;
        @include breakpoint(sm) {
            font-size: $h6-font-size;
        }
    }
    &-list {
        width: 500px;
        justify-content: flex-end;
        @include breakpoint(lg) {
            width: 100%;
        }
    }
    &-link {
        border: 1px solid $primary;
        color: $primary;
        background: $gray-100;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        padding: 4px 12px;
        position: relative;
        flex-shrink: 0;
        @include breakpoint(sm) {
            font-size: $h7-font-size;
        }
        &::after {
            content: "";
            width: 100%;
            height: 100%;
            display: block;
            position: absolute;
            left: 0;
            top: 0;
            border: 1px solid $white;
            border-radius: 9px;
            transition: all 0.2s ease;
        }
        img {
            width: 20px;
            margin-right: 4px;
            filter: invert(33%) sepia(95%) saturate(640%) hue-rotate(134deg) brightness(60%) contrast(102%);
        }
        &:hover {
            &:after {
                border: 1px solid $primary;
                border-radius: 7px;
            }
        }
    }
}
.slide-list {
    width: 90%;
    max-width: 1286px;
    margin: 0 auto;
    padding: 0 68px;
    @include breakpoint(lg) {
        max-width: 100%;
        width: 100%;
        padding: 0 72px;
    }
    @include breakpoint(xl) {
        padding: 0 calc(5% + 8px);
    }
    @include breakpoint(sm) {
        padding: 0 32px;
    }
    &-btn {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background: $white;
        border-radius: 10px;
        border: 1px solid $primary-light !important;
        box-shadow: 0 5px 7px $gray-300;
        height: 160px;
        font-size: $h6-font-size;
        font-weight: $font-weight-medium;
        color: $gray-700;
        margin: 16px auto;
        width: calc(100% - 24px);
        padding: 12px 16px;
        @include breakpoint(lg) {
            font-size: $h7-font-size;
            margin: 12px auto;
            width: calc(100% - 24px);
            height: 130px !important;
        }
        @include breakpoint(md) {
            font-size: $h6-font-size;
            margin: 8px auto;
            width: calc(100% - 16px);
            height: 110px !important;
        }
        @include breakpoint(sm) {
            padding: 8px 16px 12px 16px;
            width: calc(100% - 8px);
        }
        &:hover {
            border: 1px solid $primary !important;
            box-shadow: none;
        }
        span {
            color: $primary;
            font-size: $h5-font-size;
            font-weight: $font-weight-heavy;
            border-bottom: 1px solid $primary;
            margin-bottom: 12px;
            @include breakpoint(lg) {
                font-size: $h6-font-size;
            }
        }
        p {
            padding: 0 4px !important;
            font-size: $h5-font-size;
            line-height: 1.3;
            @include breakpoint(lg) {
                font-size: $h6-font-size;
            }
        }
    }
    .slick-dots {
        bottom: -24px;
        text-align: right;
        padding-right: 8px;
        @include breakpoint(md) {
            text-align: center;
        }
        li,
        button {
            @include breakpoint(md) {
                width: 8px !important;
                height: 8px !important;
            }
        }
    }
    p {
        padding: 0 16px;
        text-align: center;
    }
}
.inner-form-box {
    img {
        filter: brightness(500%);
        width: 30px;
        height: 30px;
        @include breakpoint(md) {
            width: 24px;
        }
    }
    select {
        background: url(../images/ic-select-down-lg.svg) no-repeat right center #fff !important;
    }
    .select2-container {
        width: 100% !important;
    }
}
.step {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    &-item {
        width: 100%;
        @include breakpoint(sm) {
            width: auto;
        }
    }
    &-item:first-child {
        .step-link {
            &.done {
                &::before {
                    display: none;
                }
            }
        }
    }
    &-item:last-child {
        .step-link {
            &.done {
                .step-txt {
                    color: $primary;
                }
            }
        }
    }
    &-number {
        display: block;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        border: 3px solid $gray-400;
        color: $gray-400;
        font-size: $h5-font-size;
        margin-bottom: 4px;
        font-weight: $font-weight-semibold;
        background: $white;
        position: relative;
        z-index: 10;
        @include breakpoint(md) {
            width: 28px;
            height: 28px;
            font-size: $h6-font-size;
            border: 2px solid $gray-400;
        }
    }
    &-txt {
        color: $black;
        text-align: center;
        @include breakpoint(md) {
            font-size: $h9-font-size;
        }
    }
    &-link {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        @include breakpoint(md) {
            padding: 0 1px;
        }
        &:hover {
            .step-number {
                border: 3px solid $gray-500;
                color: $gray-500;
            }
        }
        &:before {
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 1px;
            border-bottom: 3px dashed $gray-500;
            right: calc(50% + 20px);
            top: calc(50% - 16px);
            z-index: 1;
            @include breakpoint(md) {
                width: 100px;
                border-bottom: 2px dashed $gray-500;
                right: calc(50% + 12px);
                top: 14px;
            }
            @include breakpoint(sm) {
                width: 100%;
                min-width: 60px;
            }
        }
        &.done {
            .step-number {
                border: 3px solid $primary;
                background: $primary;
                color: $white;
            }
            &:before {
                border-bottom: 3px solid $primary;
                @include breakpoint(md) {
                    border-bottom: 2px solid $primary;
                }
            }
        }
        &.active {
            .step-number {
                border: 3px solid $primary;
                color: $primary;
            }
            .step-txt {
                color: $primary;
            }
            &:before {
                border-bottom: 3px dashed $primary;
                @include breakpoint(md) {
                    border-bottom: 2px dashed $primary;
                }
            }
        }
    }
}
.file-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background: $white;
    border: 1px solid $primary;
    border-radius: 5px;
    &[data-con="list"] {
        border-bottom: 1px solid $primary-light !important;
        border-radius: 5px 5px 0 0 !important;
    }
    &:hover {
        img {
            filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(288deg) brightness(500%) contrast(200%);
        }
    }
    & ~ .file-list {
        border-top: none;
        border-radius: 0 0 5px 5px;
    }
}
.file {
    width: 100%;
    background: $white;
}

.file-list {
    width: 100%;
    border: 1px solid $primary;
    border-radius: 5px;
    &-item {
        display: flex;
        align-items: start;
        justify-content: space-between;
        padding: 8px 12px 8px 24px;
        @include breakpoint(md) {
            padding: 8px 8px 8px 16px;
            font-size: $h7-font-size;
        }
        span {
            margin-top: 1px;
            flex-shrink: 0;
        }
    }
    &-action {
        display: block;
        background: url("../images/ic-delete.svg") no-repeat center;
        @include text-indent;
        width: 30px;
        height: 30px;
        flex-shrink: 0;
        margin-top: -4px;
        &:hover {
            opacity: 0.8;
        }
    }
}
a.file-list-item {
    color: $gray-900;
    font-weight: $font-weight-medium;
    &:hover {
        opacity: 0.8;
    }
}
.theme-box {
    box-shadow: 0 5px 5px $gray-500;
    &-header {
        background: $primary;
        color: $white;
        font-weight: $font-weight-medium;
        font-size: $h5-font-size;
        padding: 4px 24px;
    }
    &-body {
        border: 1px solid $gray-200;
        background: $white;
    }
}
.map {
    position: relative;
    border: 1px solid $gray-500;
    height: 365px;
    &-icon {
        background: url("../images/ic-locate.svg") no-repeat center;
        width: 38px;
        height: 48px;
        position: absolute;
        @include text-indent;
    }
}
.accordion-page {
    .accordion-body {
        padding: 0;
    }
    .accordion-item {
        &:last-child {
            .accordion-button {
                border-bottom: 1px solid $gray-400;
            }
        }
    }
    .accordion-button {
        color: $gray-700;
        padding: 12px 16px !important;
        border: 1px solid $gray-400;
        border-bottom: 1px solid $white;
        border-radius: 0 !important;
        font-size: $h6-font-size;
        &:after {
            right: 16px !important;
            background: url("../images/ic-down.svg") no-repeat center !important;
        }
        &:not(.collapsed) {
            border: 1px solid $primary;
            border-left: 5px solid $primary;
            color: $primary;
            &::after {
                transform: rotate(180deg);
            }
        }
    }
    .accordion-list {
        display: flex;
        flex-direction: column;
        &-btn {
            width: 100%;
            background: $primary-light !important;
            border-radius: 0 !important;
            border: none !important;
            text-align: left;
            font-size: $h7-font-size;
            padding: 10px 16px;
            &.active {
                background: $primary-xxlight !important;
            }
        }
    }
}
.form-inlin-group {
    display: flex;
    input,
    select {
        @include breakpoint(lg) {
            width: auto;
        }
    }
    .select2 {
        margin-bottom: 8px;
        margin-right: 8px;
    }
    .select2-selection__rendered {
        border: 1px solid $primary;
        border-radius: 7px;
    }
    .select2-selection__arrow {
        border-radius: 7px;
    }
}
.form-top {
    .select2 {
        @include breakpoint(md) {
            width: 100% !important;
        }
    }
    .select2-selection__rendered {
        border: 1px solid $primary;
        border-radius: 7px;
    }
    .select2-selection__arrow {
        border-radius: 7px;
    }
}
.reservation-top {
    .select2 {
        margin-right: 16px;
        @include breakpoint(lg) {
            margin-bottom: 16px;
        }
    }
    .select2-selection__rendered {
        border: 1px solid $primary;
        border-radius: 7px;
    }
    .select2-selection__arrow {
        border-radius: 7px;
    }
}
.list-message {
    display: flex;
    justify-content: center;
    align-items: center;
