    @include breakpoint(md) {
        flex-direction: column-reverse;
    }
    &-main {
        display: flex;
        @include breakpoint(md) {
            width: 100%;
        }
    }
    &-link {
        @include breakpoint(md) {
            width: 100%;
        }
    }
}
.mobile-inline-100 {
