            width: 100%;
            margin-bottom: 8px;
        }
    }
}
.view-btn {
    width: 40px;
    background: url("../images/ic-unview.svg") no-repeat center !important;
    border-left: 0 !important;
    @include text-indent;
    &.active {
        background: url("../images/ic-view.svg") no-repeat center !important;
    }
}
.highlight-card {
    display: flex;
    align-items: center;
    color: $white;
    margin: 0 -48px;
    left: 0;
    top: 32px;
    padding: 16px 48px;
    @include breakpoint(md) {
        padding: 12px 24px;
        margin: 0 -24px;
    }
    @include breakpoint(sm) {
        padding: 12px 16px;
        margin: 0 -16px;
        font-size: $h7-font-size;
    }
    &-title {
        font-size: $h3-font-size;
        font-weight: $font-weight-bold;
        @include breakpoint(md) {
            font-size: $h4-font-size;
        }
        @include breakpoint(sm) {
            font-size: $h5-font-size;
        }
    }
}
.status-list {
    &-item {
        border-radius: 8px;
        border: 1px solid $primary-light;
        display: flex;
        padding: 12px 0;
        position: relative;
        margin-bottom: 12px;
        &:last-child {
            margin-bottom: 0;
            &::after {
                display: none;
            }
        }
        &::after {
            content: "";
            background: $secondary;
            width: 1px;
            height: 12px;
            position: absolute;
            left: 90px;
            bottom: -12px;
            @include breakpoint(md) {
                left: 50%;
            }
        }
        @include breakpoint(md) {
            flex-direction: column;
            padding: 0 12px;
        }
    }
    &-title {
        font-weight: $font-weight-bold;
        width: 160px;
        border-right: 1px solid $primary-light;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $h6-font-size;
        flex-shrink: 0;
        padding: 6px 0;
        @include breakpoint(md) {
            width: 100%;
            border-right: 0;
            border-bottom: 1px solid $primary-light;
            padding: 10px 0;
        }
    }
    &-txt {
        display: flex;
        width: 100%;
        align-items: center;
        font-size: $h7-font-size;
        padding: 0 16px;
        @include breakpoint(md) {
            padding: 12px 0;
        }
    }
}
.form-top {
    @include breakpoint(md) {
        font-size: $h7-font-size;
        input,
        input::placeholder,
        select {
            font-size: $h7-font-size;
        }
    }
}
.login {
    background: url("../images/login-bg.jpg") no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    padding-top: 32px;
    @include breakpoint(sm) {
        background: url("../images/login-bg-mobile.jpg") no-repeat;
        background-size: cover;
    }
    &-title {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: $h3-font-size;
        font-weight: $font-weight-bold;
        @include breakpoint(md) {
            font-size: $h4-font-size;
        }
        img {
            margin-right: 16px;
            @include breakpoint(md) {
                width: 50px;
            }
        }
    }
    &-list {
        &-item {
            margin-bottom: 36px;
            @include breakpoint(md) {
                margin-bottom: 16px;
            }
        }
        &-link {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 12px 16px;
            border-radius: 50px;
            box-shadow: 0 4px 6px $gray-500;
            background: $white;
            font-size: $h5-font-size;
            font-weight: $font-weight-semibold;
            color: $gray-800;
            border: 2px solid $primary-light;
            @include breakpoint(lg) {
                border-width: 1px !important;
                padding: 10px 16px;
            }
            @include breakpoint(md) {
                font-size: $h6-font-size;
                padding: 8px 16px;
            }
            &:hover {
                border: 2px solid $primary;
            }
            img {
                margin-right: 12px;
                @include breakpoint(md) {
                    width: 24px;
                }
            }
        }
    }
    [data-con="login"] {
        display: flex;
    }
}
[data-con="login"] {
    display: none;
}
.link-card-list {
    display: flex;
    flex-wrap: wrap;
    @include breakpoint(md) {
        display: block;
    }
    &-item {
        padding: 8px;
        width: 25%;
        @include breakpoint(xl) {
            width: 33.33%;
        }
        @include breakpoint(lg) {
            width: 50%;
        }
        @include breakpoint(md) {
            width: 100%;
        }
    }
    &-link {
        display: block;
        border-radius: 8px;
        border: 1px solid $primary-light;
        box-shadow: 0 5px 7px $gray-300;
        background: $white;
        padding: 16px 24px;
        height: 100%;
        &:hover {
            box-shadow: none;
            border: 1px solid $primary;
        }
    }
    &-info {
        display: flex;
        color: $gray-600;
        align-items: flex-start;
        font-size: $h7-font-size;
        img {
            width: 20px;
            margin-right: 4px;
            margin-top: 2px;
        }
    }
    &-title {
        color: $gray-800;
        font-size: $h6-font-size;
        font-weight: $font-weight-semibold;
        margin-bottom: 0;
    }
    .slick-track {
        margin: 0;
    }
    .slick-arrow {
        background-image: url("../images/ic-chevron_right-white.svg");
        background-color: $primary;
        @include breakpoint(md) {
            width: 24px;
            height: 56px;
            background-size: 24px;
        }
    }
    .slick-prev {
        left: -48px;
        @include breakpoint(md) {
            left: -24px;
        }
    }
    .slick-next {
        right: -48px;
        @include breakpoint(md) {
            right: -24px;
        }
    }
    .slick-dots {
        display: none;
        bottom: -32px;
        @include breakpoint(md) {
            bottom: -24px;
        }
        li,
        button {
            width: 12px;
            height: 12px;
            @include breakpoint(md) {
                width: 8px;
                height: 8px;
            }
        }
    }
}
.sitemap {
    display: flex;
    flex-wrap: wrap;
    &-title {
        display: block;
        font-size: 1.1rem;
        color: $primary;
        font-weight: $font-weight-semibold;
        margin-bottom: 12px;
        @include breakpoint(sm) {
            font-size: $h6-font-size;
            margin-bottom: 8px;
        }
    }
}
.text-list {
    &-item {
        display: flex;
        @include breakpoint(sm) {
            flex-direction: column;
        }
        span {
            text-align: right;
            flex-shrink: 0;
            @include breakpoint(sm) {
                text-align-last: left;
            }
        }
    }
}
.fs-mobile-h7 {
    @include breakpoint(sm) {
        font-size: $h7-font-size;
    }
}
.dropdown-btn {
    &:after {
        display: none;
    }
    &.show {
        background: $secondary;
    }
    & ~ .dropdown-menu {
        border: 1px solid $secondary;
        position: relative;
        margin-top: 13px !important;
        left: -35px !important;
        @include breakpoint(md) {
            left: auto !important;
            right: 0 !important;
        }
        &::before {
            content: "";
            width: 22px;
            height: 13px;
            background: url("../images/dropdwon-point.svg") no-repeat;
            background-size: cover;
            position: absolute;
            left: calc(50% - 11px);
            top: -12px;
            @include breakpoint(md) {
                left: 112px;
            }
        }
        &::after {
            content: "";
            width: 22px;
            height: 1px;
            background: $white;
            position: absolute;
            left: calc(50% - 11px);
            top: 0px;
            @include breakpoint(md) {
                left: 112px;
            }
        }
        .dropdown-item {
            display: flex;
            align-items: center;
            color: $gray-900;
            font-weight: $font-weight-medium;
            background: $white !important;
            &.active {
                .dropdown-menu-badge {
                    border: 1px solid $primary;
                    color: $white;
                    background: $primary;
                    font-weight: $font-weight-semibold;
                    img {
                        filter: brightness(200%);
                        margin-right: 4px;
                    }
                }
            }
            .dropdown-menu-badge {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 60px;
                font-size: $h7-font-size;
                padding: 2px 0;
                border: 1px solid $primary-light;
                color: $primary;
                margin-right: 8px;
                img {
                    margin-right: 5px;
                }
            }
        }
    }
}
.base-btn {
    display: flex;
    align-items: center;
    background: $white;
    border-radius: 8px;
    border: 1px solid $white !important;
    box-shadow: 0 2px 7px $gray-500;
    font-size: $h6-font-size;
    font-weight: $font-weight-medium;
    &:hover {
        border: 1px solid $primary !important;
        box-shadow: none;
    }
    img {
        width: 20px;
        margin-right: 4px;
    }
}
