.modal-dialog{
    max-width: 90%;
    overflow: hidden;
    margin-left: auto;
    margin-right: auto;
    @include breakpoint(lg){
        max-width: 700px;
    }
    .btn-close{
        background: none;
        img{
            width: 16px;
        }
    }
}
.modal-content{
    border-radius: 24px;
    overflow: hidden;
    @include breakpoint(lg){
        border-radius: 32px;
    }
}
.modal-slider{
    .slick-dots{
        margin-top: -8px;
        background: $white;
        padding: 16px 0;
        li{
            width: 20px;
            border-radius: 15px;
        }
        li button{
            width: 20px;
            height: 8px;
            border-radius: 15px;
            &:before{
                width: 20px;
            border-radius: 15px;
            }
        }
    }
    img{
        width: 100%;
        height: 500px;
        object-fit: contain;
        margin: 0 auto;
        @include breakpoint(xl){
            height: 700px;
        }
    }
}
/*--------------------------------------*/
.mfp-content {
    max-width: 1000px !important;
    position: relative;
    .mfp-figure {
        overflow: hidden;
        background: $white;
        border-radius: 20px;
        img {
            border-radius: 10px;
            padding: 0;
            margin: 40px 16px 16px 16px;
            width: calc(100% - 32px);
        }
    }
    .mfp-title {
        display: none;
    }
    .mfp-figure:after {
        border-radius: 20px;
    }
    .mfp-counter{
        display: none;
    }
    .mfp-close {
        position: absolute;
        right: 16px !important;
        top: 0;
        color: $gray-800 !important;
    }
    figcaption {
        display: block;
        width: 100%;
        height: auto;
        padding-bottom: 12px;
    }
    .mfp-labels{
        display: inline-block;
        position: relative;
        line-height: 1.2;
        margin-bottom: 8px;
        li{
            display: inline-block;
            color: $primary-dark;
            border: 1px solid $primary-dark;
            border-radius: 50px;
            padding: 3px 8px 1px 8px;
            font-size: $h8-font-size;
            margin-right: 8px;
            @include breakpoint(lg){
                padding: 4px 12px 1px 12px;
                font-size: $h7-font-size;
            }
        }
    }
    .mfp-bottom-bar{
        position: relative;
        margin-top: 0;
        padding: 0 16px;
    }
    .mfp-description{
        display: inline;
    line-height: 1.2;
    padding-right: 24px;
    font-size: $h7-font-size;
    @include breakpoint(lg){
        font-size: $h6-font-size;
    }
    }
}
.mfp-arrow{
    opacity: 1;
    width: 34px;
    height: 88px;
    &:after{
        display: none;
    }
    &-left{
        border-radius: 0 10px 10px 0;
        left: 10px;
        background: url("../images/ic-slide-arrow-left.svg") $white no-repeat center !important;
    }
    &-right{
        border-radius: 10px 0 0 10px;
        right: 10px;
        background: url("../images/ic-slide-arrow-right.svg") $white no-repeat center !important;
    }
}
.product{
    background: $bright;
    position: relative;
    padding-bottom: 180px;
    margin-bottom: -70px;
    &::before{
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 62px;
        left: 0;
        top: -62px;
        background: url("../images/related-top.svg");
    }
    &-title{
        font-family: "Noto Serif TC", serif;
        text-align: center;
        position: relative;
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 300px;
        margin: 0 auto;
        font-weight: $font-weight-bold;
        &:after{
            content: "";
            display: block;
            position: absolute;
            width: 100%;
            height: 1px;
            border-bottom: 2px dashed $gray-700;
            top: 45px;
            left: 0;
        }
        &-main{
            display: inline;
            background: $bright;
            padding: 0 8px;
            position: relative;
            z-index: 100;
            font-weight: $font-weight-bold;
        }
    }
    &-img{
        border-radius: 24px;
        border: 1px solid $secondary;
        margin-bottom: 8px;
        width: 100%;
        height: 180px;
        object-fit: cover;
        @include breakpoint(sm){
            height: 200px;
        }
        @include breakpoint(lg){
            height: 240px;
        }
    }
    &-link{
        text-align: center;
        font-weight: $font-weight-bold;
    }
    &-info{
        color: $primary-xdark;
        display: block;
    }    &-name{
        color: $black;
    }
    .slick-slide{
        padding: 0 8px;
    }
    .slick-arrow{
        top: 40%;
    }
    .slick-next{
        right: -24px;
    }
    .slick-prev{
        left: -24px;
    }
}